/* eslint-disable */
import { Component,director,Director } from 'cc';
import { app } from '../../app/app';
import { EDITOR,EDITOR_NOT_IN_PREVIEW } from 'cc/env';

export type IReadOnly<T> = { readonly [P in keyof T]: T[P] extends Function ? T[P] : (T[P] extends Object ? IReadOnly<T[P]> : T[P]); };

export type IViewName = "PageHome"|"PageMain"|"PageOver"|"PageRoom"|"PopCommondialog"|"PopIdentity"|"PopTip"
export type IViewNames = IViewName[]
export type IMiniViewName = "PaperHomeIndex"|"PaperRoomRoom"
export type IMiniViewNames = IMiniViewName[]
export type IMusicName = "never"
export type IMusicNames = IMusicName[]
export type IEffectName = "effect/button"
export type IEffectNames = IEffectName[]

import config_const from '../app-model/config.const'
import store_user from '../app-model/store.user'
import {HomeController} from '../app-controller/HomeController'
import {RoomController} from '../app-controller/RoomController'
import {ApiManager} from '../app-manager/api/ApiManager'
import {UtilManager} from '../app-manager/util/UtilManager'
import EventManager from '../../../extensions/app/assets/manager/event/EventManager'
import LoaderManager from '../../../extensions/app/assets/manager/loader/LoaderManager'
import SoundManager from '../../../extensions/app/assets/manager/sound/SoundManager'
import TimerManager from '../../../extensions/app/assets/manager/timer/TimerManager'
import UIManager from '../../../extensions/app/assets/manager/ui/UIManager'
export type IApp = {
    Controller: {Home:typeof HomeController,Room:typeof RoomController},
    controller: {home:IReadOnly<HomeController>,room:IReadOnly<RoomController>},
    Manager: {Api:Omit<typeof ApiManager,keyof Component>,Util:Omit<typeof UtilManager,keyof Component>,Event:Omit<typeof EventManager,keyof Component>,Loader:Omit<typeof LoaderManager,keyof Component>,Sound:Omit<typeof SoundManager,keyof Component>,Timer:Omit<typeof TimerManager,keyof Component>,UI:Omit<typeof UIManager,keyof Component>},
    manager: {api:Omit<ApiManager,keyof Component>,util:Omit<UtilManager,keyof Component>,event:Omit<EventManager,keyof Component>,loader:Omit<LoaderManager,keyof Component>,sound:Omit<SoundManager<IEffectName,IMusicName>,keyof Component>,timer:Omit<TimerManager,keyof Component>,ui:Omit<UIManager<IViewName,IMiniViewName>,keyof Component>},
    data: {},
    config: {const:IReadOnly<config_const>}
    store: {user:IReadOnly<store_user>}
}

function init(){
if(!EDITOR||!EDITOR_NOT_IN_PREVIEW) Object.assign(app.config, {const:new config_const()})
if(!EDITOR||!EDITOR_NOT_IN_PREVIEW) Object.assign(app.data, {})
if(!EDITOR||!EDITOR_NOT_IN_PREVIEW) Object.assign(app.store, {user:new store_user()})

if(!EDITOR||!EDITOR_NOT_IN_PREVIEW) Object.assign(app.Controller, {Home:HomeController,Room:RoomController})
if(!EDITOR||!EDITOR_NOT_IN_PREVIEW) Object.assign(app.controller, {home:new HomeController(),room:new RoomController()})
}
if(!EDITOR||!EDITOR_NOT_IN_PREVIEW) director.on(Director.EVENT_RESET,init)
if(!EDITOR||!EDITOR_NOT_IN_PREVIEW) init()
